import { LinearGradient } from "expo-linear-gradient";
import React, { useEffect } from "react";
import { ImageBackground, View } from "react-native";
import { requestUrlS3 } from "../../services/utils";
import { layoutConstants } from "../../themes/layout-contants";
import { IconButton, Text } from "@react-native-material/core";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { headerStyle } from "../../themes/component-style";

export function HeaderBackground({ children, onPress, title }) {
  const [headerBgURL, setHeaderBgURL] = React.useState(null);

  useEffect(() => {
    const getBgSync = async () => {
      const url = await requestUrlS3(layoutConstants.HOME_BG_URLS[1]);
      setHeaderBgURL(url);
    };
    getBgSync();
  }, []);

  return (
    <View style={headerStyle.container}>
      <LinearGradient
        style={{ flex: 1 }}
        colors={["#247770", "#1B625E", "#114D4C"]}
      >
        <ImageBackground
          source={{ uri: headerBgURL }}
          resizeMode='stretch'
          style={{ flex: 1 }}
          imageStyle={{ opacity: 0.5 }}
        >
          <View
            style={{
              display: "flex",
              justifyContent: 'center',
              alignItems: "center",
              flexDirection: "row",
              height: "100%",
              margin: 10
            }}
          >
            <IconButton
              onPress={onPress}
              icon={(props) => (
                <Icon name="arrow-left" {...props} color={"#7BFFFF"} />
              )}
            />
            <Text
              variant="h5"
              style={{ flex: 1, textAlign: "center", color: "#7BFFFF", paddingRight: 30 }}
            >
              {title}
            </Text>
            {children}
          </View>
        </ImageBackground>
      </LinearGradient>
    </View>
  );
}
