import { LinearGradient } from "expo-linear-gradient";
import { ImageBackground, View } from "react-native";
import { ViewLandscape } from "./view-landscape";
import React, { useEffect } from "react";
import { requestUrlS3 } from "../../services/utils";
import { layoutConstants } from "../../themes/layout-contants";


export function ViewBackground({ children, noScroll1 = true, noScroll2 = true }) {
  const [headerBgURL, setHeaderBgURL] = React.useState(null);

  useEffect(() => {
    const getBgSync = async () => {
      const url = await requestUrlS3(layoutConstants.HOME_BG_URLS[0]);
      setHeaderBgURL(url);
    };
    getBgSync();
  }, []);

  return (
    <View style={{ flex: 1 }}>
      <ViewLandscape noScroll={true}>
        <LinearGradient style={{flex: 1}} colors={["#328C83", "#165D58", "#135B56", "#083B3C"]}>
          <ImageBackground
            source={{ uri: headerBgURL }}
            resizeMode="cover"
            style={{ flex: 1 }}
            imageStyle={{ opacity: 0.5 }}
          >
            <ViewLandscape noScroll={true}>
              {children}
            </ViewLandscape>
          </ImageBackground>
        </LinearGradient>
      </ViewLandscape>
    </View>
  );
}
