import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { IconButton } from "@react-native-material/core";
import React from "react";
import { Pressable, StyleSheet, View } from "react-native";
import { componentConstants } from "./component-contants";
import { layoutConstants } from "../../themes/layout-contants";

const BottomBarCustom = (props) => {
  const { page, navigation } = props;
  const isHome = page == componentConstants.HOME_SCREEN;
  const isAction = page == componentConstants.ACTIONS_SCREEN;
  const isSetting = page == componentConstants.USER_SETTINGS_SCREEN;
  return (
    <View style={styles.containerStyle}>
      <View
        style={{
          height: "100%",
          margin: "auto",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          width: '100%',
          justifyContent: 'center'
        }}
      >
        <Pressable
          style={[styles.tab, isAction ? styles.activeTab : ""]}
          onPress={() => {
            navigation.navigate(componentConstants.ACTIONS_SCREEN);
          }}
        >
          <Icon
            size={30}
            name="playlist-check"
            color={isAction ? styles.activeIcon.color : styles.icon.color}
          />
        </Pressable>
        <Pressable
          style={[styles.tab, isHome ? styles.activeTab : ""]}
          onPress={() => {
            navigation.navigate(componentConstants.HOME_SCREEN);
          }}
        >
          <Icon
            size={30}
            name="home"
            color={isHome ? styles.activeIcon.color : styles.icon.color}
          />
        </Pressable>
        <Pressable
          style={[styles.tab, isSetting ? styles.activeTab : ""]}
          onPress={() => {
            navigation.navigate(componentConstants.USER_SETTINGS_SCREEN);
          }}
        >
          <Icon
            size={30}
            name="bell-alert"
            color={isSetting ? styles.activeIcon.color : styles.icon.color}
          />
        </Pressable>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  containerStyle: {
    position: "absolute",
    right: 0,
    bottom: 0,
    left: 0,
    height: 60,
    backgroundColor: "#E8FFFD",
    borderTopLeftRadius: 20,
    borderTopRightRadius: 20,
  },
  icon: {
    color: "#3FA8A0",
  },
  activeIcon: {
    color: layoutConstants.buttonTextColorPrimary,
  },
  activeTab: {
    backgroundColor: layoutConstants.buttonBgColorPrimary,
  },
  tab: {
    height: "100%",
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
    maxWidth: 200
  },
});

export default BottomBarCustom;
