// somewhere in your app
import { Text } from "@react-native-material/core";
import {
  Menu,
  MenuOption,
  MenuOptions,
  MenuTrigger,
} from "react-native-popup-menu";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import { componentConstants } from "../utils/component-contants";
import AsyncStorage from "@react-native-async-storage/async-storage";
import React from "react";
import { useIsFocused } from "@react-navigation/core";
import { lineStyle } from "../../themes/component-style";

export function UserMenu({ navigation }) {
  const getAsyncStorageItem = async (key) => {
    return await AsyncStorage.getItem(key);
  };
  const [userInfo, setUserInfo] = React.useState(null);
  const deleteLocalStorage = () => {
    AsyncStorage.removeItem("user");
    setUserInfo(null);
  };
  const focus = useIsFocused();
  React.useEffect(() => {
    const getUserSync = async () => {
      const user = await getAsyncStorageItem("user");
      setUserInfo(user);
    };
    getUserSync();
  }, [focus]);

  return (
    <Menu>
      <MenuTrigger>
        <Icon name={"menu"} size={30} color={"#3BFFFF"}></Icon>
      </MenuTrigger>
      <MenuOptions>
        <MenuOption
          style={[lineStyle.line, { padding: 10 }]}
          onSelect={() => {
            navigation.navigate("UserSettings");
          }}
        >
          <Text>Settings</Text>
        </MenuOption>
        {!userInfo && componentConstants.HAS_USER_SIGN_IN && (
          <MenuOption
            style={{ padding: 10 }}
            onSelect={() => {
              navigation.navigate("UserSignIn");
            }}
          >
            <Text>Sign In</Text>
          </MenuOption>
        )}
        {!!userInfo && (
          <MenuOption
            style={{ padding: 10 }}
            onSelect={() => {
              deleteLocalStorage();
            }}
          >
            <Text>Log Out</Text>
          </MenuOption>
        )}
        {componentConstants.HAS_USER_PAYMENT && (
          <MenuOption
            style={{ padding: 10 }}
            onSelect={() => {
              navigation.navigate("UserPayment");
            }}
          >
            <Text>Payment</Text>
          </MenuOption>
        )}
      </MenuOptions>
    </Menu>
  );
}
